<template>
    <!--=== Products ===-->
    <section class="products">
        <div class="container">
            <div class="row">
                <div class="col-12">
                    <div class="section-header">
                        <div class="sec-icon"><i class="ion ion-android-list"></i></div>
                        <div class="sec-title">Product display
                        </div>
                    </div>
                </div>
                <div class="col-lg-4 col-md-4 col-sm-6" v-for="(item,index) in productsList" :key="index">
                    <div class="product-wrap">
                        <div class="img-wrap">
                            <img :src="imgUrl+item.path" alt="Product Item" style="width: 20rem; height: 15rem;" />
                            <div class="product-overlay">
                                <a href="javascript:;" @click="$router.push({name:'Shop'})" class="icon-wrap"><i class="ion ion-android-cart"></i></a>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="col-12">
                    <div class="see-more"><a href="javascript:;" class="btn btn-primary main-btn bg-main" @click="more">show more</a></div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
    export default {
        name: "products",
        data:()=>({
            productsList:[
                {
                    src:require('@/images/product-thumb.jpg')
                },
                {
                    src:require('@/images/product-thumb-2.jpg')
                },
                {
                    src:require('@/images/product-thumb-3.jpg')
                },
                {
                    src:require('@/images/product-thumb.jpg')
                },
                {
                    src:require('@/images/product-thumb-2.jpg')
                },
                {
                    src:require('@/images/product-thumb-3.jpg')
                }
            ]
        }),
        mounted(){
            this.getShop();
        },
        methods:{
            more(){
                // this.productsList.push(  {
                //         src:require('@/images/product-thumb-2.jpg')
                //     },
                //     {
                //         src:require('@/images/product-thumb-3.jpg')
                //     })
            },
            getShop(){
                // this.loading();
                let _self = this;
                _self.$ajax.post('/api/homepagegoods',{})
                    .then(res=>{
                        if(res.resultCode === 1){
                            _self.productsList = res.resultData
                            // this.closeLoading();
                        }
                    })
            }
        }
    }
</script>

